<template>
  <div class="container">
        <div class="intro">
          <span class="intro_msg1">   ?</span>
          <span class="intro_msg2">    .</span>
        </div>
  </div>
  <ScatterChart />
</template>
<script>
import tsne_data from '../../data.json';
import { defineComponent, h, PropType } from 'vue'
import { Scatter } from 'vue-chartjs'
import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  LineElement,
  CategoryScale,
  LinearScale,
  PointElement,
  Plugin
} from 'chart.js'

ChartJS.register(
  Title,
  Tooltip,
  Legend,
  LineElement,
  CategoryScale,
  LinearScale,
  PointElement
)

export default defineComponent({
  name: 'ScatterChart',
  components: {
    Scatter
  },
  props: {
    chartId: {
      type: String,
      default: 'scatter-chart'
    },
    width: {
      type: Number,
      default: 800
    },
    height: {
      type: Number,
      default: 800
    },
    cssClasses: {
      default: '',
      type: String
    },
    styles: {
      type: Object ,
      default: () => {}
    }
  },
  setup(props) {
    const chartData = {
      datasets: [
        {
          label: 'University',
          fill: true,
          borderColor: '#f87979',
          backgroundColor: '#f87979',
          data: tsne_data,
          pointRadius: 5
        },
      ]
    }

    const chartOptions = {
      responsive: true,
      maintainAspectRatio: false,
      plugins: {
        title: {
                display: false,
                font: {
                  size: 30
                },
                text: 't-SNE Visualization of University',
                padding: 100,
              },
      tooltip: {
            intersect: true,
            usePointStyle: true,
            callbacks: {
                    labelPointStyle: function(context) {
                        return {
                            pointStyle: 'triangle',
                            rotation: 0
                        };
                    },
                    label: function(context) {
                        return context.raw.name
                    }
            }
      }
        },
      scales: {
        x: {
          grid: {
            display: false
          }
        },
        y: {
          grid: {
            display: false
          }
        }
      },
      onClick: function(point){
          console.log(point.chart.tooltip.dataPoints[0].raw.name);
        },
      layout: {
            padding: {
                left: 50,
                right: 50,
                bottom: 50
            }
        }
      
    }
    return () =>
      h(Scatter, {
        chartData,
        chartOptions,
        chartId: props.chartId,
        width: props.width,
        height: props.height,
        cssClasses: props.cssClasses,
        styles: props.styles
      })
  }
})
</script>
